<template>
  <div class="main-container">
    <div class="title-container">
      <input v-model="title" placeholder="Edit title">
    </div>
    
    <div class="timer-container"
      v-bind:class="{
        red: started && !reversed && duration < 60,
        green: started && reversed
      }">

      <span class="sign" v-if="started && reversed">-</span>
      <input class="input-timer hours"
        :disabled="started"
        :value="hours | padNumber"
        @input="updateValue('hours', $event.target.value)">
      <span class="separator">:</span>
      <input class="input-timer minutes"
        :disabled="started"
        :value="minutes | padNumber"
        @input="updateValue('minutes', $event.target.value)">
      <span class="separator">:</span>
      <input class="input-timer seconds"
        :disabled="started"
        :value="seconds | padNumber"
        @input="updateValue('seconds', $event.target.value)">

    </div>
    <div class="bottom-container">
      <div class="buttons-container"
        v-bind:class="{
          hidden: started && !paused,
        }">
        <button v-if="!started" v-on:click="startTimer">Start</button>
        <button v-if="started && !paused" v-on:click="pauseTimer">Pause</button>
        <button v-if="started && paused" v-on:click="resumeTimer">Resume</button>
        <button v-if="started" v-on:click="resetTimer">Reset</button>
        <button v-if="!started && token" v-on:click="syncTimer">Sync from Online</button>
      </div>
      <div class="token-buttons-container"
        v-bind:class="{
          hidden: !edittoken,
        }">
        <button v-if="!started && !edittoken" v-on:click="editToken">Use Token</button>
        <input v-if="edittoken" class="input-token token"
          placeholder="Input CLOCKTOKEN"
          :value="token"
          @input="updateToken($event.target.value)">
        <button v-if="!started && edittoken" v-on:click="submitEditToken">Submit</button>
        <button v-if="!started && edittoken" v-on:click="cancelEditToken">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script>
var axios = require('axios');
import miniToastr from 'mini-toastr';
import * as proto from './../../static/clock_pb.js';

miniToastr.init()

export default {
  name: 'main',
  data () {
    return {
      title: '',
      token: this.$route.query['token'] || '',
      duration: 0,
      hours: 0,
      minutes: 50,
      seconds: 0,
      paused: true,
      started: false,
      edittoken: false,
      reversed: false,
      interval: undefined,
      resetHours: 0,
      resetMinutes: 50,
      resetSeconds: 0
    }
  },
  filters: {
    padNumber: function (value) {
      if (value < 10) {
        return '0' + value;
      } else {
        return value;
      }
    }
  },
  created: function () {
    if (this.token) {
      this.syncTimer();
    }
  },
  methods: {
    editToken: function () {
      this.edittoken = true;
    },
    cancelEditToken: function () {
      this.edittoken = false;
    },
    submitEditToken: function () {
      this.edittoken = false;
    },
    updateToken: function (value) {
      this.token = value.toString();
      this.$forceUpdate();
    },
    syncTimer: function () {
      var gctreq = new proto.GetClockTimeRequest();
      gctreq.setToken(this.token);

      var decoder = new TextDecoder('utf8');
      var b64encoded = btoa(decoder.decode(gctreq.serializeBinary()));
      var parent = this;

      axios.post('https://clock.mtgpairings.info/api/dev/gettime', b64encoded)
      .then(function (response) {
        miniToastr.success('Online time received');
        var gctrep = proto.GetClockTimeReply.deserializeBinary(response.data);

        parent.title = gctrep.getTitle();

        parent.hours = gctrep.getHours()
        parent.minutes = gctrep.getMinutes();
        parent.seconds = gctrep.getSeconds();
        parent.calculateDuration();

        parent.reversed = gctrep.getNegative();

        if (gctrep.getStatus() === proto.StartClock.Status.RUNNING) {
          parent.started = true;
          parent.paused = false;
          parent.startInterval();
        }
        // 16701ec7-f7b7-4de8-bf90-2146c92e019e
      });
//      .catch(function (error) {
//        miniToastr.error(error.response['data']);
        // TODO: Make it stay longer
//      });
    },
    startTimer: function () {
      this.started = true;
      this.paused = false;
      this.resetHours = this.hours;
      this.resetMinutes = this.minutes;
      this.resetSeconds = this.seconds;
      this.calculateDuration();
      this.startInterval();

      var sc = new proto.StartClock();
      sc.setToken(this.token);
      sc.setHours(this.hours);
      sc.setMinutes(this.minutes);
      sc.setSeconds(this.seconds);
      sc.setTitle(this.title);

      var decoder = new TextDecoder('utf8');
      var b64encoded = btoa(decoder.decode(sc.serializeBinary()));

      axios.post('https://clock.mtgpairings.info/api/dev/startclock', b64encoded)
      .then(function (response) {
        miniToastr.success('Token successfully linked');
      })
      .catch(function (error) {
        miniToastr.error(error.response['data']);
        // TODO: Make it stay longer
      });
    },
    pauseTimer: function () {
      this.paused = true;
      this.stopInterval();

      var sc = new proto.StartClock();
      sc.setToken(this.token);
      sc.setStatus(proto.StartClock.Status.STOPPED);

      var decoder = new TextDecoder('utf8');
      var b64encoded = btoa(decoder.decode(sc.serializeBinary()));
      var parent = this;

      axios.post('https://clock.mtgpairings.info/api/dev/startclock', b64encoded)
      .then(function (response) {
        miniToastr.success('Bot clock paused');
      })
      .catch(function (error) {
        miniToastr.error(error.response['data']);
        // TODO: Make it stay longer
      });
    },
    resumeTimer: function () {
      this.paused = false;
      this.startInterval();

      var sc = new proto.StartClock();
      sc.setToken(this.token);
      sc.setStatus(proto.StartClock.Status.RUNNING);
      sc.setHours(this.hours);
      sc.setMinutes(this.minutes);
      sc.setSeconds(this.seconds);
      sc.setNegative(this.reversed);

      var decoder = new TextDecoder('utf8');
      var b64encoded = btoa(decoder.decode(sc.serializeBinary()));
      var parent = this;

      axios.post('https://clock.mtgpairings.info/api/dev/startclock', b64encoded)
      .then(function (response) {
        miniToastr.success('Bot clock restarted');
      })
      .catch(function (error) {
        miniToastr.error(error.response['data']);
        // TODO: Make it stay longer
      });
    },
    resetTimer: function () {
      this.reversed = false;
      this.started = false;
      this.paused = true;
      this.stopInterval();
      this.hours = this.resetHours;
      this.minutes = this.resetMinutes;
      this.seconds = this.resetSeconds;
      this.duration = 0;

      var sc = new proto.StartClock();
      sc.setToken(this.token);
      sc.setStatus(proto.StartClock.Status.STOPPED);
      sc.setHours(this.hours);
      sc.setMinutes(this.minutes);
      sc.setSeconds(this.seconds);

      var decoder = new TextDecoder('utf8');
      var b64encoded = btoa(decoder.decode(sc.serializeBinary()));
      var parent = this;

      axios.post('https://clock.mtgpairings.info/api/dev/startclock', b64encoded)
      .then(function (response) {
        miniToastr.success('Bot clock reset');
      })
      .catch(function (error) {
        miniToastr.error(error.response['data']);
        // TODO: Make it stay longer
      });
    },
    calculateDuration: function () {
      this.duration = (this.hours * 3600) + (this.minutes * 60) + this.seconds;
    },
    setTime: function (duration) {
      this.hours = Math.floor(duration / 3600);
      this.minutes = Math.floor(duration / 60) % 60;
      this.seconds = duration % 60;
    },
    startInterval: function () {
      this.interval = setInterval(() => {
        if (this.duration <= 0) {
          this.reversed = true;
        }

        if (!this.reversed) {
          this.duration -= 1;
        } else {
          this.duration += 1;
        }

        this.setTime(this.duration);
      }, 1000);
    },
    stopInterval: function () {
      window.clearInterval(this.interval);
    },
    updateValue: function (field, value) {
      var formattedValue = Number(value);

      if (isNaN(formattedValue) || !Number.isInteger(formattedValue)) {
        formattedValue = this[field];
      } else if (formattedValue > 59 && field !== 'hours') {
        formattedValue = this[field];
      } else if (formattedValue < 0 && field !== 'hours') {
        formattedValue = this[field];
      } else if (formattedValue > 99 && field === 'hours') {
        formattedValue = this[field];
      }

      this[field] = formattedValue;
      this.$forceUpdate();
    }
  }
}
</script>

<style>
  @font-face {
    font-family: 'Roboto';
    src: url('../../static/fonts/Roboto-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  .main-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .title-container {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-shrink: 0;
    width: 100%;
    flex-grow: 0.5;
    margin: 0;
    z-index: 1;
  }
  .title-container > input {
    width: 100%;
    font-size: 5vw;
    text-align: center;
    border: none;
    background: transparent;
  }
  .timer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 26vw;
    font-size: 26vw;
    cursor: default;
    color: black;
    overflow: hidden;
    flex-grow: 1;
  }
  .timer-container.red {
    color: red; 
  }
  .timer-container.red .input-timer:disabled {
    -webkit-text-fill-color:red;
  }
  .timer-container.green {
    color: green;
  }
  .timer-container.green .input-timer:disabled {
    -webkit-text-fill-color:green;
  }
  .input-token {
    font-family: "Roboto";
    font-size: 30px;
    line-height: 71px;
    width: 300px;
    height: 71px;
    border: none;
    background: transparent;
    padding: 0px 6px;
  }
  .input-timer {
    font-family: "Roboto";
    font-size: 24vw;
    width: 26vw;
    height: 25vw;
    border: none;
    background: transparent;
  }
  .input-timer:disabled {
    color: unset;
    -webkit-text-fill-color:#000000
  }
  /*.input-timer.seconds {
    text-align: left;
  }
  .input-timer.minutes {
    text-align: right;
  }*/
  .sign {
    margin-top: -3vw;
    user-select: none;
    font-size: 18vw;
  }
  .separator {
    margin-top: -2vw;
    user-select: none;
  }
  .bottom-container {
    height: 200px;
  }
  .buttons-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 109px;
    z-index: 1;
    padding: 0 0 0 0;
    margin-bottom: 10px;
  }
  .buttons-container.hidden > button {
    opacity: 0;
    pointer-events: none;
  }
  .buttons-container:hover > button {
    opacity: 1;
    pointer-events: all;
  }
  .buttons-container > button {
    font-size: 60px;
    padding: 20px 40px;
    margin: 0 16px 0 16px;
    border-radius: 16px;
    border: none;
    color: white;
    background-color: cornflowerblue;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;
    text-transform: uppercase;
  }
  .token-buttons-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 80px;
    z-index: 1;
  }
  .token-buttons-container.hidden > button {
    opacity: 0.3;
    pointer-events: none;
  }
  .token-buttons-container:hover > button {
    opacity: 1;
    pointer-events: all;
  }
  .token-buttons-container > button {
    font-size: 35px;
    padding: 15px 20px;
    margin: 0 16px 0 16px;
    border-radius: 12px;
    border: none;
    color: white;
    background-color: cornflowerblue;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;
    text-transform: uppercase;
  }
</style>



